.container {
  width: 100%;
  margin-bottom: 20px;
  border: 15px solid #f2f8f8;
  border-top: 5px solid #f2f8f8;
  border-collapse: collapse;
}

.head {
  &__title {
    font-weight: 400;
    padding: 5px;
    background: #f2f8f8;
    border: none;
    border-bottom: 5px solid #f2f8f8;
    font-family: 'Lato';
    font-size: 20px;
    color:#141515
  }
}

.info {
  padding: 5px;
  border: none;
  border-bottom: 5px solid #f2f8f8;
  font-family: 'PT Sans';
  font-size: 16px;
  color:#434848
}
